body { font-family: "opensans-regular", sans-serif; font-size: 18px; line-height: 1.4; background: url("../img/bg.jpg") repeat-y; height: 2000px; }

.out { padding-top: 75px; }

.l-center, .header__in { width: 1630px; max-width: 100%; margin: 0 auto; padding: 0 15px; position: relative; }

a { text-decoration: none; }

.btn { line-height: 1; font-family: "opensans-regular", sans-serif; font-size: 18px; color: #fff; background: #e9a84e; padding: 0.72222em 1.61111em; border-radius: 1.2em; }
.btn_mod { font-size: 12px; font-family: "opensans-semibold", sans-serif; text-transform: uppercase; color: #202441; }

.hamburger { display: inline-block; vertical-align: middle; cursor: pointer; position: relative; transition: border-color 0.3s; height: 75px; width: 75px; padding: 10px; background: #fff; }
.hamburger span { width: 34px; height: 28px; display: inline-block; border: 3px solid #000; border-width: 3px 0; position: relative; }
.hamburger span:after { content: ""; position: absolute; top: 50%; left: 0; height: 3px; background: #000; width: 100%; margin-top: -1px; transition: background 0.3s; }

h1, .h1 { color: #e9a84e; font-family: "opensans-regular", sans-serif; font-weight: 100; font-size: 36px; margin: 1em 0; text-align: center; text-transform: uppercase; }

.header { position: fixed; top: 0; left: 0; right: 0; *zoom: 1; }
.header:after { content: " "; display: table; clear: both; }
.header__in { height: 75px; background: linear-gradient(-96deg, #202642 0%, #201b3b 100%); text-align: center; font-size: 0; }
.header__in:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.header__logo { color: #fff; font-size: 50px; float: left; line-height: 1; position: absolute; width: 23.125%; text-align: right; left: 0; top: 50%; margin-top: -0.5em; }
.header__btn { display: inline-block; vertical-align: middle; text-align: center; }
.header .callback { position: absolute; right: 0; top: 0; }

.callback { font-size: 20px; }
.callback__link { color: #fff; font-family: "opensans-bold", sans-serif; font-size: 24px; margin-right: 15px; line-height: 1; }


